.page {
  font-family: Manrope, Arial, sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  color: #1b2129;
  font-size: 16px;
  line-height: 22.4px;
  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden; 
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transition: all 0.3s;
    opacity: 0;
    pointer-events: none;
    transform: translateX(-100%);

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }
}
